
@media (max-width: 768px) {
  .site-content {
    padding: 0px; /* Lägg till lite padding på mindre skärmar */
    align-items: stretch; /* Strecka ut barnen för att ta upp all tillgänglig bredd */
  }
}
.site-container{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  
}
.site-content{
  display: flex;
  flex: 1;
  flex-direction: column;
  min-height: 100vh;
  background-color:#fefae0;
  align-items: center;
}
.site-footer{
text-align: center;
padding: 5px;
background-color:#344e41;
color: white;
}
/*Contact*/
form {
  display: flex;
  flex-direction: column;
  background-color: rgba(250, 237, 205);
  margin: 20px;
  padding: 20px;
  width: 150%;
  border: 1px solid rgba(250, 237, 205);
  border-radius: 7px;
}
@media (max-width: 768px) {
  form {
      width: 100%; 
      margin: 10px auto; 
      padding: 15px; 
  }
  input[type=text], input[type=email], select, textarea {
      width: 100%; 
  }
  input[type=email] {
      width: 50%; 
  }

}    
label{
display: block;
padding-top: 10px;
}
input[type="submit"] {
background-color:#344e41;
padding: 10px 15px;
width: 25%;
border: none;
border-radius: 4px;
cursor: pointer;
}
/*hoover for send button*/
input[type=submit]:hover {
background-color:#588157;
color: white;
}
input[type=submit]{
color: white;
}
input[type=text], [type=email] ,select, textarea {
width: 60%; /* half width */
padding: 10px; /*padding */ 
border: 1px solid whitesmoke; /* border color */
border-radius: 4px; /* Rounded borders */
box-sizing: border-box; /* Make sure that padding and width stays in place */
resize: vertical 
}
.kontaktbild{
  margin-top: 20px;
  max-width: 300px;
  max-height: 300px;
  width: 100%;
  height: auto;
  object-fit:cover;
  border-radius: 50%;
  overflow: hidden;
}
.project-image {
  border: #fefae0;
  border-radius: 15px;
  width: 550px;
  height: 550px; 
  object-fit: cover; 
  overflow: hidden;
}
@media (max-width: 768px) {
  .kontaktbild,
  .project-image,
  .green3,
  .about-Image {
      max-width: 100%; 
      height: auto; 
  }
  
}

.kontakt{
  display: flex;
  justify-content: center;
 
}
.port{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.Home{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  max-width: 900px;
}
.aboutPage{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 900px;
}
