.cv-container {
    display: flex;
    width: 100%;
}
li{
    list-style: none;
}
.left-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    align-items: flex-start;
    padding-top: 50px;
    min-height: 100vh;
    background-color: rgba(250, 237, 205);
}
.right-section h2{
    padding-left: 20px;
    margin-left: 20px;
}
.right-section p{
    padding-left: 20px;
    margin-left: 20px;
}
.left-section h2{
    padding-left: 20px;
    margin-left: 20px;
}
.left-section p{
    padding-left: 20px;
    margin-left: 20px;
}

ul li{
    list-style: none;
    padding: 2px;
}
.right-section {
    flex: 1;
    display: flex;
    background-color:#fefae0;
    padding-top: 50px;
    align-items: flex-start;
    box-sizing: border-box;
    flex-direction: column;
    min-height: 100vh;
}
.profile-picture {
    max-width:275px;
    max-height: 275px;
    width: 100%;
    object-fit:cover;
    border-radius: 50%;
    overflow: hidden;
    padding-left: 20px;
}

@media (max-width: 768px) {
    .cv-section .cv-container {
        flex-direction: column; /* Ändra layouten till kolumn vid mindre skärmbredd */
    }
    .left-section,
    .right-section {
        flex: 1;
        padding: 25px; 
    }
}
@media (max-width: 768px) {
    .right-section {
        flex-direction: column; 
        align-items: center;  
        padding: 25px; 
    }
    .profile-picture {
        width: auto;
        height: auto;
    }
}


